.content {
    display: flex;
    flex-direction: column;
    flex: 1;
    height: 90vh;
    margin: 0.5rem;
    background-color: var(--edit-screen);
}

.allText {
    width: 100%;
    height: 100%;
    font-size: 1.05rem;
    flex: 1;
    overflow-y: scroll;
}

pre {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    margin: 0.8rem;
    outline: none;
    color: var(--font-col);
    min-height: 30%;
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word;
    line-height: 170%;
}

.btnset {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 3rem;
    background-color: var(--body-background);
    user-select: none;
}

.btnset div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2rem;
    width: 5rem;
    border: 2px solid var(--font-col);
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.btnset div:active {
    background-color: var(--menu-box);
}

@media screen and (max-width:1110px) {
    .content {
        position: relative;
        height: 25rem;
        width: 100vw;
        margin: 0rem;
    }
}