header {
    position: sticky;
    top: 0;
    left: 0;
    width: 100vw;
    background-color: var(--body-background);
    height: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    user-select: none;
    z-index: 999;
}

header .hero {
    font-size: 1.3rem;
    font-weight: bold;
    margin-left: 2rem;
}

.options ul {
    padding: 0;
    list-style: none;
    display: flex;
    margin-right: 2rem;
}

.options li {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2rem;
    width: 7rem;
    border: 2px solid var(--font-col);
    border-radius: 0.3rem;
    margin-right: 2rem;
    font-weight: bolder;
    cursor: pointer;
}

header li:active {
    background-color: var(--title-highlight);
}

@media screen and (max-width:1110px) {
    header {
        position: sticky;
        top: 0;
        left: 0;
        width: 100vw;
        min-height: 3rem;
        margin-top: 0;
        z-index: 999;
    }

    header .hero {
        margin-left: 1.2rem;
        font-size: 1.6rem;
        color: var(--font-col);
    }

    .menuContainer {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.534);
        z-index: 800;
        display: none;
    }

    .menuContainer.active {
        display: flex;
    }

    .menuItems {
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: var(--menu-box);
        top: 4rem;
        left: 100vw;
        width: 10rem;
        border-bottom-left-radius: 10px;
        border-top-left-radius: 10px;
        overflow: hidden;
        transition: 0.2s all ease;
        visibility: hidden;
        z-index: 999;
    }

    .menuItems.active {
        visibility: visible;
        transform: translateX(-100%);
        transition: 0.2s ease;
    }

    .menuItems ul {
        list-style: none;
        padding-left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .menuItems li {
        min-height: 3rem;
        width: 100%;
        flex: 1;
        background-color: inherit;
        color: var(--font-col);
        border: none;
        outline: none;
        font-size: 1.25rem;
        font-weight: bold;
        user-select: none;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .menuItems li:active {
        background-color: grey;
    }

    .bar {
        display: flex;
    }

    .menubtn-bar {
        display: flex;
        justify-content: center;
        align-items: center;
        outline: none;
        border: 0px solid black;
        border-radius: 50%;
        color: inherit;
        background-color: inherit;
        height: 2.5rem;
        width: 2.5rem;
        font-size: 1.2rem;
        font-weight: bold;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        margin-right: 0.4rem;
        transform: rotate(0deg);
        transition: all 0.3s ease;
    }

    .menubtn-bar.active {
        transform: rotate(90deg);
        transition: all 0.3s ease;

    }

    .addBtn {
        display: none;
    }

    .addbtnbar {
        height: 2.2rem;
        width: 2.2rem;
        border-radius: 1.6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bold;
        margin-right: 0.7rem;
        font-weight: bold;
        background-color: var(--plus-icon-back);
        color: black;
        align-self: center;
        user-select: none;
    }

    .addbtnbar:active {
        transform: scale(0.97);
    }

}