.item{
    display: flex;
    align-items: center;
    min-height: 3rem;
    user-select: none ;
    margin-top: 0.5rem;
}
.icont{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.7rem;
    height: 100%;
    width: 3rem;
}
.icont i{
    transition: 0.3s all ease;
}
.title{
    display: flex;
    align-items: center;
    font-size: 1.1rem;
    font-weight: bold;
}

.subitems{
    display: none;
    align-items: center;
    transition: 0.3s all ease;
}
.subitems ul {
    padding: 0;
    list-style: none;
    width: 100%;
    background-color: var(--title-highlight);
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 3rem;
}
.subitems p{
    margin-left: 1rem;
}
.subitems li{
    display: flex;
    align-items: center;
    height: 2.5rem;
    font-size: 1.1rem;
;
}
.subitems li:active{
    background-color: var(--body-background);
}

@media screen and (max-width:1110px) {
    .title{
        font-size: 1.1rem;
    }
    .icont{
        font-size: 1.5rem;
    }
}