:root {
    --body-background: #252525;
    --font-col: #fff;
    --title-highlight: #3c3c3c;
    --plus-icon-back: #C4C4C4;
    --menu-box: rgb(88, 88, 88);

    --popup-back: #1d2b2e;
    --popup-input: #273c41;

    --edit-screen: rgb(0, 61, 41);

}

[data-theme="dark"] {
    --body-background: #ffffff;
    --font-col: rgb(0, 0, 0);
    --title-highlight: #c9c9c9;
    --plus-icon-back: #C4C4C4;
    --menu-box: rgb(189, 189, 189);

    --popup-back: #88a2a7;
    --popup-input: #afced6;

    --edit-screen: rgb(91, 203, 165);
}

.App {
    background-color: var(--body-background);
    color: var(--font-col);
    height: 100vh;
    max-width: 100vw;
    overflow-x: hidden;
    overflow-y: hidden;
}

.listOfItems {
    flex: 1;
    height: 90vh;
    overflow-y: scroll;
}

.contentHolder {
    display: flex;
    justify-content: space-between;
}

@media screen and (min-width:1109px) {

    /* width */
    ::-webkit-scrollbar {
        width: 7px;
    }

    /* Track */
    ::-webkit-scrollbar-track {
        background-color: transparent;
        transition: 1s ease;
    }

    ::-webkit-scrollbar-track:hover {
        background: var(--item-back);
        border-radius: 7px;
        transition: 1s ease;
    }

    /* Handle */
    ::-webkit-scrollbar-thumb {
        background: var(--font-col);
        border-radius: 7px;
    }

    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
        background: var(--item-border);
    }
}

@media screen and (max-width:1110px) {
    .container {
        flex: 1;
        max-width: 100vw;
    }

    .contentHolder {
        flex: 1;
        display: flex;
        flex-direction: column;
        height: 90vh;
        overflow-y: scroll;
    }

    .listOfItems {
        flex: 1;
    }
}